import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ServiceProvider } from '../../providers/service/service';

/**
 * Generated class for the HowPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-how',
  templateUrl: 'how.html',
})
export class HowPage {

  data = {
    nama: '',
    friends: '',
    disampaikan: '',
    topik: '',
    media: ''
  }

  hasError = false;
  errMsg = ""
  informasi = ""

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public serviceProvider: ServiceProvider) {

      if(!navParams.get('actived')) {
        navCtrl.setRoot('BerandaPage')
      }

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HowPage');
  }

  ionViewWillEnter() {
    if (this.serviceProvider.how_page) {
      this.navCtrl.popToRoot()
    }
    this.serviceProvider.retrieveOperationalData().then((data: any) => {
      this.informasi = data.content;
    })
  }

  keHalamanSharing() {

    this.hasError = false;
    // this.navCtrl.push('SharingPage')
    // console.log('keHalamanSharing')
    console.log('data yang diinput =>', this.data)

    if (
      this.data.nama &&
      this.data.friends &&
      this.data.disampaikan &&
      this.data.topik &&
      this.data.media
    ) {


      this.serviceProvider.LOADER()

      this.serviceProvider.simpanDataResponden(this.data)
        .then((res) => {
          this.navCtrl.push('SharingPage', { person: res, actived: true })
          this.serviceProvider.Loader.dismiss()
          this.data = {
            nama: '',
            friends: '',
            disampaikan: '',
            topik: '',
            media: ''
          }
          this.serviceProvider.how_page = true;
          // alert('Yeay... datanya sudah tersimpan...')
        }, (err) => {
          // alert('Oops... ada yang ga beres nih')
          console.log(err)
          this.serviceProvider.Loader.dismiss()
        })

    }

    else {
      this.errMsg = "Pastikan semua sudah terisi yaaa..."
      this.hasError = true;
      console.log('data harus diisi')
    }

    // this.navCtrl.push('SharingPage', {data: "ben"})

  }

}



